import { Handler } from '@netlify/functions';
import { createClient } from '@supabase/supabase-js';
import OpenAI from 'openai';
import { v4 as uuidv4 } from 'uuid';

const supa = createClient(
  process.env.SUPABASE_URL as string,
  process.env.SUPABASE_SERVICE_KEY as string
);
const openai = new OpenAI({ apiKey: process.env.OPENAI_API_KEY });

export const handler: Handler = async (event) => {
  // Handle CORS preflight
  if (event.httpMethod === 'OPTIONS') {
    return {
      statusCode: 200,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST,OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    };
  }
  // Only allow POST for actual uploads
  if (event.httpMethod !== 'POST') {
    return {
      statusCode: 405,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST,OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      },
      body: 'Method Not Allowed'
    };
  }

  try {
    const { fileName, base64 } = JSON.parse(event.body || '{}');
    if (!fileName || !base64) {
      return {
        statusCode: 400,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'POST,OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        },
        body: 'Missing fileName or base64 data'
      };
    }

    // Generate unique ID and storage path
    const id = uuidv4().slice(0, 8);
    const storagePath = `collages/${id}-${fileName}`;

    // Upload to Storage
    const buffer = Buffer.from(base64, 'base64');
    const { error: uploadErr } = await supa.storage
      .from('images')
      .upload(storagePath, buffer, { upsert: true });
    if (uploadErr) {
      console.error('Storage upload error:', uploadErr);
      return { statusCode: 500, body: JSON.stringify({ error: uploadErr.message }) };
    }

    // Get public URL
    const { data: urlData } = supa.storage.from('images').getPublicUrl(storagePath);
    const publicUrl = urlData.publicUrl;

    // Insert initial row
    const { error: insertErr } = await supa.from('images').insert({
      id,
      src: publicUrl,
      title: fileName,
      tags: []
    });
    if (insertErr) {
      console.error('DB insert error:', insertErr);
      return { statusCode: 500, body: JSON.stringify({ error: insertErr.message }) };
    }

    // Call OpenAI to generate metadata
    const prompt =
      'Analyze this collage image. Provide a detailed description of its composition, textures, and artistic elements. Also suggest 5 relevant tags that capture its essence and classify it as either "texture", "narrative", or "conceptual". Format your response as JSON {"description":string, "tags":string[], "imageType":string}';
    const response = await openai.chat.completions.create({
      model: 'gpt-4o-mini',
      max_tokens: 300,
      messages: [
        { role: 'user', content: prompt },
        { role: 'user', content: `data:image/jpeg;base64,${base64}` }
      ],
    });
    const content = response.choices[0].message.content || '{}';

    let metadata: { description?: string; tags?: string[]; imageType?: string } = {};
    try {
      metadata = JSON.parse(content);
    } catch {
      // fallback parsing
      const descMatch = content.match(/"description"\s*:\s*"([^"]+)"/i);
      const tagsMatch = content.match(/"tags"\s*:\s*\[([^\]]+)\]/i);
      const typeMatch = content.match(/"imageType"\s*:\s*"([^"]+)"/i);
      metadata.description = descMatch ? descMatch[1] : '';
      metadata.tags = tagsMatch ? tagsMatch[1].split(',').map(t => t.replace(/['"\s]/g, '').trim()) : [];
      metadata.imageType = typeMatch ? typeMatch[1] : undefined;
    }

    // Update row with metadata
    const { error: updateErr } = await supa.from('images')
      .update({ description: metadata.description, tags: metadata.tags, imageType: metadata.imageType })
      .eq('id', id);
    if (updateErr) {
      console.error('DB update error:', updateErr);
      return { statusCode: 500, body: JSON.stringify({ error: updateErr.message }) };
    }

    return {
      statusCode: 200,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST,OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      },
      body: JSON.stringify({ id, src: publicUrl, ...metadata })
    };
  } catch (e: any) {
    console.error(e);
    return {
      statusCode: 500,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'POST,OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      },
      body: JSON.stringify({ error: e.message })
    };
  }
}; 